













































































































































































































































































































import mixins from "../../mixin/init";
import { Component, Mixins } from "vue-property-decorator";
import jquery from "jquery";
import _ from "lodash";
const $: any = jquery;
@Component
export default class Fame extends Mixins(mixins) {
    mounted () {
        document.title = `Hall of fame -- ${document.title}`;
        this.$Lazyload.$on("loaded", (el) => {
            const $segment = $(this.$refs.main).find(".ui.segment");
            $segment.each(function (this: any) {
                const $this = $(this);
                console.log($this);
                $this.find(".sticky").sticky({
                    context: `#${$this.attr("id")}`,
                    offset: 50
                });
            });
        });
        const $headers = $(".ui.header.contents");
        const len = $headers.length;
        const list = $("#linkedlist");
        for (let i = 0; i < len; ++i) {
            const id = $headers.eq(i).parent().attr("id");
            const name = $headers.eq(i).text();
            let ahtml = "<a name='" + id + "' ></a>";
            $headers.eq(i).append(ahtml);
            ahtml = "<a class='quick item' href='#" + id + "' >" + name + "</a>";
            list.append(ahtml);
        }
        this.$nextTick(() => {
            $(".ui.sticky.left")
                .sticky({
                    context: "#mainContent",
                    offset: 50
                });
            $(".quick.item").on("click", function (this: any) {
                $([document.documentElement, document.body]).animate({
                    scrollTop: $(this.getAttribute("href")).offset().top - 50
                }, 600);
                return false;
            });
        });
    }

    updated () {
        $(".ui.sticky.left")
            .sticky({
                context: "#mainContent",
                offset: 50
            });
    }
}
